<template>
  <div class="message" :class="messageClass">
    <div class="message-content">
      {{ message.text }}
    </div>
  </div>
</template>

<script>
export default {
  props: {
    message: {
      type: Object,
      required: true,
    },
  },
  computed: {
    messageClass() {
      return {
        'user-message': this.message.type === 'user',
        'ai-message': this.message.type === 'ai',
      };
    },
  },
};
</script>

